<template>
<div>
    <div class="tree">
        <div class="treeContent">
            <p>当前用户菜单权限</p>
            <el-tree default-expand-all :data="roleMenu" :props="roleMenuDefaultProps"></el-tree>
        </div>
        <div class="treeContent">   
            <p>所有菜单权限</p>
            <el-tree
                ref='tree'
                :data="allMenu"
                show-checkbox
                node-key="menuId"
                default-expand-all
                :default-checked-keys="sameList"
                :props="allMenuDefaultProps">
            </el-tree>
        </div>
    </div>
    <div class="submitBtn">
        <el-button type="primary" @click="submitMenu">提交</el-button>
    </div>
</div>
</template>

<script>
import { get_role_getMenu_api, get_all_menu_api, get_role_saveMenu_api } from '@/api/system';
import { getTrees, getSame } from '@/lib/utils';
export default {
    data() {
        return {
            sameList: [], // 已存在的菜单
            allMenu: [], // 所有菜单
            roleMenu: [], // 角色菜单
            roleMenuDefaultProps: {
                children: 'children',
                label: 'name'
            },
            allMenuDefaultProps: {
                children: 'children',
                label: 'name'
            }
        }
    },
    methods: {
        getData () {
            Promise.all([
                get_role_getMenu_api({roleId:this.$route.params.id}),
                get_all_menu_api()
            ]).then(res => {
                this.roleMenu = getTrees(res[0],-1);
                this.allMenu = getTrees(res[1],-1);
                this.sameList = getSame(res[0],res[1]);
            });
        },
        submitMenu () {
            let arr = [];
            this.$refs.tree.getCheckedNodes().forEach(ele => {
                arr.push(ele.menuId);
            });
            let obj = {
                roleId:this.$route.params.id,
                menuIds: arr
            }
            get_role_saveMenu_api(obj).then(() => {
                this.$message({
                    message: "修改成功",
                    type: 'success'
                })
                this.getData();
            })
        }
    },
    created() {
        this.getData();
    }
}
</script>

