    display: flex;
    .treeContent{
        width:50%;
        flex:1;
        border:1px solid #eee;
        padding:10px;
        p{
            text-align: center;
            font-size: 20px;
            margin-bottom: 20px;
        }
        &:last-child{
            margin-left: -1px;
        }
    }
}
.submitBtn{
    text-align: center;
    margin-top: 20px;
}
</style>